.hub {
    width: 100%;
    height: 5rem;
    background-color: var(--hub);
    display: flex;
    align-items: center;
    padding-left: 1rem;
}

.hub-icon {
    font-size: 2rem;
    color: var(--gray);
}

.hub-icon:hover {
    cursor: pointer;
    color: var(--white);
}

.hub-title {
    color: var(--baby-powder);
    font-size: 2em;
    font-weight: bold;
    padding-left: 2em;
}